/* CKEditor 5 Styling for Letter Editor */
@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+Arabic:wght@400;500;600;700&display=swap');

/* Editor Container */
.ckeditor-container {
  width: 100%;
  max-width: 100%;
  min-width: 0;
  flex: 1;
  display: flex;
  flex-direction: column;
  border: 1px solid #CED4DA;
  border-radius: 0.5rem;
  overflow: hidden;
  background: #fff;
  min-height: 650px;
}

.ckeditor-container.is-invalid {
  border-color: #dc3545;
}

/* CKEditor 5 specific styling */
.ckeditor-container .ck-editor {
  width: 100%;
  max-width: 100%;
}

.ckeditor-container .ck-editor__main {
  width: 100%;
  /* max-width: 100%; */
}

.ckeditor-container .ck-editor__editable {
  min-height: 600px !important;
  max-height: 800px !important;
  height: 600px !important;
  overflow-y: auto;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  font-size: 14px;
  line-height: 1.6;
  background: #fff;
  border: none;
  padding: 24px;
}

/* RTL direction when language is Arabic */
.rtl .ckeditor-container .ck-editor__editable {
  direction: rtl;
  text-align: right;
}

/* LTR direction when language is English */
.ltr .ckeditor-container .ck-editor__editable {
  direction: ltr;
  text-align: left;
}

/* RTL Support for Arabic text */
.ckeditor-container .ck-editor__editable[dir="rtl"] {
  direction: rtl;
  text-align: right;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}

.ckeditor-container .ck-editor__editable[dir="ltr"] {
  direction: ltr;
  text-align: left;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}


/* Toolbar styling */
.ckeditor-container .ck-toolbar {
  background: #F8F9FA;
  border-bottom: 1px solid #E5E7EB;
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
  padding: 8px;
  flex-wrap: wrap;
  gap: 5px;
}

.ckeditor-container .ck-toolbar__separator {
  background: #dee2e6;
  margin: 0 4px;
}

/* Button styling */
.ckeditor-container .ck-button {
  border-radius: 3px;
  transition: all 0.2s ease;
  min-width: 28px;
  height: 28px;
}

.ckeditor-container .ck-button:hover {
  background-color: #e9ecef;
  transform: scale(1.05);
}

.ckeditor-container .ck-button.ck-on {
  background-color: #B68A35;
  color: white;
  box-shadow: 0 2px 4px rgba(212, 170, 58, 0.3);
}

/* Dropdown styling */
.ckeditor-container .ck-dropdown__panel {
  background: white;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  z-index: 1000;
}

.ckeditor-container .ck-list__item {
  padding: 6px 12px;
  cursor: pointer;
  text-align: right;
}

.ckeditor-container .ck-list__item:hover {
  background-color: #f0f0f0;
}

/* Table styling */
.ckeditor-container .ck-editor__editable table {
  border-collapse: collapse;
  margin: 10px 0;
  width: 100%;
  max-width: 100%;
  table-layout: fixed;
  direction: rtl;
}

.ckeditor-container .ck-editor__editable table td,
.ckeditor-container .ck-editor__editable table th {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: right;
  vertical-align: top;
  min-width: 50px;
  word-wrap: break-word;
  position: relative;
}

.ckeditor-container .ck-editor__editable table th {
  background-color: #f5f5f5;
  font-weight: bold;
}

/* Table selection styling */
.ckeditor-container .ck-editor__editable table .table-selected {
  background-color: rgba(0, 123, 255, 0.1) !important;
  border: 2px solid #007bff !important;
}

/* Table toolbar */
.ckeditor-container .ck-table-toolbar {
  background: white;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  padding: 5px;
  z-index: 1000;
  min-width: 200px;
}

.ckeditor-container .ck-table-toolbar button {
  margin: 2px;
  padding: 6px 12px;
  font-size: 12px;
  border: 1px solid #ddd;
  background: white;
  cursor: pointer;
  border-radius: 3px;
  display: block;
  width: 100%;
  text-align: right;
}

.ckeditor-container .ck-table-toolbar button:hover {
  background: #f0f0f0;
}

/* Font family dropdown */
.ckeditor-container .ck-font-family-dropdown {
  min-width: 120px;
}

/* Font size dropdown */
.ckeditor-container .ck-font-size-dropdown {
  min-width: 80px;
}

/* Color picker */
.ckeditor-container .ck-color-picker {
  width: 28px;
  height: 28px;
  padding: 0;
  border: 1px solid #ccc;
  border-radius: 3px;
}

/* Special characters panel */
.ckeditor-container .ck-special-characters-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(30px, 1fr));
  gap: 2px;
  padding: 10px;
  max-height: 200px;
  overflow-y: auto;
}

.ckeditor-container .ck-special-characters-grid button {
  width: 30px;
  height: 30px;
  border: 1px solid #ddd;
  background: white;
  cursor: pointer;
  border-radius: 3px;
  font-size: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ckeditor-container .ck-special-characters-grid button:hover {
  background: #f0f0f0;
}

/* Page break styling */
.ckeditor-container .ck-editor__editable .page-break {
  display: block;
  margin: 20px 0;
  text-align: center;
  border: none;
  border-top: 2px dashed #ccc;
  color: #666;
  font-size: 12px;
}

.ckeditor-container .ck-editor__editable .page-break::before {
  content: "--- Page Break ---";
  background: white;
  padding: 0 10px;
}

/* Block quote styling */
.ckeditor-container .ck-editor__editable blockquote {
  border-left: 4px solid #ced4da;
  padding-left: 15px;
  margin-left: 0;
  margin-right: 15px;
  font-style: italic;
  color: #6c757d;
  direction: rtl;
  border-right: 4px solid #ced4da;
  border-left: none;
  padding-right: 15px;
  padding-left: 0;
}

/* Code block styling */
.ckeditor-container .ck-editor__editable pre {
  background-color: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 4px;
  padding: 12px;
  font-family: 'Courier New', Courier, monospace;
  font-size: 13px;
  line-height: 1.4;
  overflow-x: auto;
  direction: ltr;
  text-align: left;
}

/* Base list styling */
.ckeditor-container .ck-editor__editable ul,
.ckeditor-container .ck-editor__editable ol {
  margin: 0 0 1rem 0;
}

.ckeditor-container .ck-editor__editable ul {
  list-style-type: disc;
}

.ckeditor-container .ck-editor__editable ol {
  list-style-type: decimal;
}

.ckeditor-container .ck-editor__editable li {
  margin: 0;
  padding: 0.25rem 0;
}

/* RTL list styling for Arabic */
.rtl .ckeditor-container .ck-editor__editable ul,
.rtl .ckeditor-container .ck-editor__editable ol {
  padding-inline-start: 0;
  padding-inline-end: 2.5rem;
  direction: rtl;
}

.rtl .ckeditor-container .ck-editor__editable ol {
  padding-inline-end: 3rem;
}

.rtl .ckeditor-container .ck-editor__editable li {
  padding-inline-end: 0.5rem;
}

.rtl .ckeditor-container .ck-editor__editable ol li {
  padding-inline-end: 0.5rem;
  margin-inline-end: 0.5rem;
}

/* LTR list styling for English */
.ltr .ckeditor-container .ck-editor__editable ul,
.ltr .ckeditor-container .ck-editor__editable ol {
  padding-inline-start: 2.5rem;
  padding-inline-end: 0;
  direction: ltr;
}

.ltr .ckeditor-container .ck-editor__editable ol {
  padding-inline-start: 3rem;
}

.ltr .ckeditor-container .ck-editor__editable li {
  padding-inline-start: 0.5rem;
}

.ltr .ckeditor-container .ck-editor__editable ol li {
  padding-inline-start: 0.5rem;
  margin-inline-start: 0.5rem;
}

/* Heading styling */
.ckeditor-container .ck-editor__editable h1,
.ckeditor-container .ck-editor__editable h2,
.ckeditor-container .ck-editor__editable h3,
.ckeditor-container .ck-editor__editable h4,
.ckeditor-container .ck-editor__editable h5,
.ckeditor-container .ck-editor__editable h6 {
  margin: 0 0 0.5rem 0;
  font-weight: 600;
  line-height: 1.2;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}

.ckeditor-container .ck-editor__editable h1 { font-size: 2rem; }
.ckeditor-container .ck-editor__editable h2 { font-size: 1.75rem; }
.ckeditor-container .ck-editor__editable h3 { font-size: 1.5rem; }
.ckeditor-container .ck-editor__editable h4 { font-size: 1.25rem; }
.ckeditor-container .ck-editor__editable h5 { font-size: 1rem; }
.ckeditor-container .ck-editor__editable h6 { font-size: 0.875rem; }

/* Link styling */
.ckeditor-container .ck-editor__editable a {
  color: #B68A35;
  text-decoration: none;
}

.ckeditor-container .ck-editor__editable a:hover {
  text-decoration: underline;
}

/* Responsive design */
@media (max-width: 768px) {
  .ckeditor-container .ck-editor__editable {
    min-height: 450px;
    max-height: 600px;
    padding: 12px;
  }
  
  .ckeditor-container .ck-toolbar {
    padding: 4px;
    flex-wrap: wrap;
  }
  
  .ckeditor-container .ck-button {
    min-width: 24px;
    height: 24px;
    font-size: 12px;
  }
}
.ck-content ol {
  list-style-type: decimal !important;
  direction: rtl;
  unicode-bidi: bidi-override;
  padding-right: 20px;
}

.ck-content ul {
  list-style-type: disc !important;
  direction: rtl;
  unicode-bidi: bidi-override;
  padding-right: 20px;
}
/* Focus states */
.ckeditor-container:focus-within {
  border-color: #B68A35   ;
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgba(212, 170, 58, 0.25);
}

/* Invalid state */
.ckeditor-container.is-invalid {
  border-color: #dc3545;
}

.ckeditor-container.is-invalid:focus-within {
  border-color: #dc3545;
  box-shadow: 0 0 0 0.25rem rgba(220, 53, 69, 0.25);
}
